.CreateTodoButton{
    background-color: #e7c100;
    box-shadow: 0px 0px 25px #bd9e04;
    border: none;
    border-radius: 50%;
    cursor: pointer;
    font-size: 50px;
    position: fixed;
    bottom: 24px;
    right: 24px;
    font-weight: bold;
    color: black;

    align-items: center;
    height: 64px;
    width: 64px;

    transform: rotate(0);
    transition: .3s ease;
    z-index: 1;
}

.CreateTodoButton:hover{
    transform: rotate(224deg);
}